import React from 'react';
import ReactDOM from 'react-dom';

const blue = {
    '☆': 0.5,
    '☆☆': 0.95,
    '☆☆☆': 1,
}

const blue_type = {
    'スピード': 0.2,
    'スタミナ': 0.4,
    'パワー': 0.6,
    '根性': 0.8,
    '賢さ': 1,
}

function choose(random_variable) {
    const dice = Math.random();
    for (const [x, p] of Object.entries(random_variable).sort()) {
        if (dice <= p) {
            return x;
        }
    }
    console.assert(0);
}

class App extends React.Component {
    constructor() {
        super();
        this.state = {
            result: 'N/A'
        };
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        this.setState({
            result: choose(blue_type) + choose(blue),
        });
    }

    render() {
        return (
            <div className='App'>
                <div className='button'>
                    <input type='button' value='start' onClick={this.handleClick} />
                </div>
                <div className='result'>
                    {this.state.result}
                </div>
            </div>
        );
    }
}

ReactDOM.render(
    <App />,
    document.getElementById('root')
);